'use client';

import { createContext, useContext, useState, ReactNode, useMemo, useCallback } from 'react';

type SkillStatus = 'scheduled' | 'ongoing' | 'completed';
type SkillType = 'learned' | 'taught';

interface Skill {
  id: string;
  title: string;
  partnerName: string;
  date: string;
  duration: number;
  rating: number;
  status: SkillStatus;
  type: SkillType;
}

type PlantContextType = {
  // Plant state
  level: number;
  trades: number;
  progressPercentage: number;
  nextStageThreshold: number;
  currentStage: 'seed' | 'sprout' | 'flowering' | 'fruiting';
  
  // Skills state
  skills: Skill[];
  updateSkillStatus: (id: string, status: SkillStatus) => void;
  
  // Update function
  updateTrades: (count: number) => void;
};

const PlantContext = createContext<PlantContextType | undefined>(undefined);

export const PlantProvider = ({ children }: { children: ReactNode }) => {
  const [trades, setTrades] = useState(0);
  const [skills, setSkills] = useState<Skill[]>([
    {
      id: '1',
      title: 'Web Development',
      partnerName: 'Alex Johnson',
      date: '2025-11-14',
      duration: 1,
      rating: 0,
      status: 'scheduled',
      type: 'learned',
    },
    {
      id: '2',
      title: 'Spanish Language',
      partnerName: 'Matei Lungu',
      date: '2025-11-15',
      duration: 1,
      rating: 0,
      status: 'scheduled',
      type: 'learned',
    },
  ]);
  
  const maxLevel = 20; // Level at which the plant reaches its final form
  
  // 10 trades = 5 levels = plant growth
  const level = Math.min(Math.floor((trades / 10) * 5), maxLevel); // Cap at max level
  
  // Calculate plant growth stage based on level
  const getCurrentStage = (): 'seed' | 'sprout' | 'flowering' | 'fruiting' => {
    if (level < 5) return 'seed';      // Levels 0-4: Seed (0-10 trades)
    if (level < 10) return 'sprout';   // Levels 5-9: Sprout (10-20 trades)
    if (level < 15) return 'flowering'; // Levels 10-14: Flowering (20-30 trades)
    return 'fruiting';                 // Levels 15-20: Fruiting (30-40 trades)
  };

  const currentStage = useMemo(() => getCurrentStage(), [level]);
  
  // Calculate progress to next level (0-100%)
  // Each level takes 2 trades to complete
  const progressPercentage = (trades % 2) * 50; // 0% or 50% per trade
  
  // Calculate trades in current stage
  const tradesInCurrentStage = 
    level < 5 ? trades : // 0-4 levels (0-10 trades) = seed
    level < 10 ? trades - 10 : // 5-9 levels (10-20 trades) = sprout
    level < 15 ? trades - 20 : // 10-14 levels (20-30 trades) = flowering
    trades - 30; // 15-20 levels (30-40 trades) = fruiting
  
  // Calculate next stage threshold (in trades)
  // Each stage is 5 levels, which is 10 trades (since 2 trades = 1 level)
  const nextStageThreshold = 
    level < 5 ? 10 :  // 0-4 levels (0-10 trades) = seed
    level < 10 ? 20 : // 5-9 levels (10-20 trades) = sprout
    level < 15 ? 30 : // 10-14 levels (20-30 trades) = flowering
    40;               // 15-20 levels (30-40 trades) = fruiting

  const updateTrades = useCallback((count: number) => {
    setTrades(count);
  }, []);

  const updateSkillStatus = useCallback((id: string, status: SkillStatus) => {
    setSkills(prevSkills => {
      const updatedSkills = prevSkills.map(skill => 
        skill.id === id ? { ...skill, status } : skill
      );
      
      // Update trades count based on completed skills
      const completedCount = updatedSkills.filter(skill => skill.status === 'completed').length;
      setTrades(completedCount);
      
      return updatedSkills;
    });
  }, []);

  const value = useMemo(() => {
    console.log('PlantContext updated:', { 
      level, 
      trades, 
      progressPercentage, 
      currentStage, 
      completedSkills: skills.filter(s => s.status === 'completed').length 
    });
    
    return {
      // Plant state
      level,
      trades,
      progressPercentage,
      nextStageThreshold,
      currentStage,
      
      // Skills state
      skills,
      updateSkillStatus,
      
      // Update function
      updateTrades,
    };
  }, [
    level, 
    trades, 
    progressPercentage, 
    nextStageThreshold, 
    currentStage, 
    skills,
    updateSkillStatus,
    updateTrades
  ]);

  return (
    <PlantContext.Provider value={value}>
      {children}
    </PlantContext.Provider>
  );
};

export const usePlant = (): PlantContextType => {
  const context = useContext(PlantContext);
  if (!context) {
    throw new Error('usePlant must be used within a PlantProvider');
  }
  return context;
};
